.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 5;
  background-color: rgba(0, 0, 0, 0.6);
}

.modal {
  z-index: 6;
  background-color: #fff;
  padding: 1rem;
  width: 300px;
  border-radius: var(--radii-sm);
  position: fixed;
  left: 50%;
  top: 25%;
  transform: translateX(-50%);
  overflow: hidden;

  @media (min-width: 767px) {
    width: 550px;
  }

  h2 {
    margin-top: 0;
  }
}

.modalAction {
  padding-top: 1rem;
}
